import { Component, Input, OnDestroy, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSelectChange } from '@angular/material/select';
import { Router } from '@angular/router';
import { APP_ROUTE, VALIDATOR } from '@core/constants';
import { AuthType, PlaceAdType, SellerType, UserRoles } from '@core/enums';
import { BusinessProfile, FilterValue } from '@core/models';
import { AuthDialogV2Component } from '@feature/auth/v2/auth-dialog-v2.component';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { PlaceAdService } from '@feature/place-ad/place-ad.service';
import { ButtonType } from '@shared/components/button/button.component';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, takeUntil } from 'rxjs';
import { SubscriptionOfferDialogComponent } from '../subscription-offer-dialog/subscription-offer-dialog.component';
import { ImageService } from '@core/services/image.service';
import { AgentStatus } from '@core/enums/agent.enum';
import { DEFAULT_EMAIL_PLACEHOLDER } from '@feature/my-profile/my-profile.constant';
import { MyProfileService } from '@core/services/my-profile.service';
import { MatInput } from '@angular/material/input';

@Component({
    selector: 'findqo-business-account-v2',
    templateUrl: './business-account-v2.component.html',
    styleUrls: ['./business-account-v2.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class BusinessAccountV2Component implements OnDestroy {
	constructor(
		private myProfileFacade: MyProfileFacade,
		private imageService: ImageService,
		private formBuilder: FormBuilder,
		private destroy: DestroyService,
		private dialog: MatDialog,
		private router: Router,
		private placeAdService: PlaceAdService,
		private myProfileService: MyProfileService
	) {
		this.countyList$ = this.myProfileFacade.getCountyList$();
		this.countyAreaList$ = this.myProfileFacade.getCountyAreaList$();
		this.business$ = myProfileFacade.getBusinessProfile$();
		this.inviterName$ = myProfileService.inviterName$;
	}
	@ViewChild('phoneNumber') phoneNumber?: MatInput;
	@Input() fromPlaceAd = false;

	readonly buttonType = ButtonType;
	readonly agentStatus = AgentStatus;
	readonly appRoute = APP_ROUTE;

	hasBusinessAccount = false;
	selectedFile: File | null = null;
	isPhotoUploading = false;
	isChangeEmail = false;
	business$: Observable<BusinessProfile>;
	countyList$: Observable<FilterValue[]>;
	countyAreaList$: Observable<FilterValue[]>;
	businessProfile: BusinessProfile;
	defaultEmailPlaceholder = DEFAULT_EMAIL_PLACEHOLDER;
	isAgentUser = false;
	inviterName$: Observable<string>;

	businessInformationForm: FormGroup = this.formBuilder.group({
		name: ['', [Validators.required, VALIDATOR.pattern.name]],
		phone: ['', [Validators.required, Validators.minLength(7), Validators.maxLength(10), Validators.pattern('^[0-9]*$')]],
		phoneCode: ['+353', Validators.required],
		countyId: ['', Validators.required],
		countyAreaId: [{ value: '', disabled: true }, Validators.required],
		address: ['', Validators.required],
	});

	searchCounty = new FormControl('');
	searchCountyArea = new FormControl('');

	saveBusinessProfile(): void {
		const FORM_DATA = this.businessInformationForm.value;

		this.myProfileFacade.updateUserBusinsessProfile(FORM_DATA);

		if (!this.hasBusinessAccount) {
			this.myProfileFacade.setShowBusinessAcc(true);
			this.myProfileFacade.loadAgents();

			this.dialog
				.open(SubscriptionOfferDialogComponent, {
					disableClose: true,
					width: '500px',
					height: '600px',
				})
				.afterClosed();
		}
	}

	onFileSelect(event: any): void {
		if (event.target.files.length === 0) {
			return;
		}

		this.selectedFile = <File>event.target.files[0];

		this.onUpload();
	}

	async onUpload(): Promise<void> {
		if (this.selectedFile) {
			this.isPhotoUploading = true;

			this.imageService.getCompressedImage(this.selectedFile, 400).then((file) => {
				this.selectedFile = file;

				this.myProfileFacade
					.uploadUserBusinsessProfilePhoto(this.selectedFile)
					.pipe(takeUntil(this.destroy))
					.subscribe(() => {
						this.isPhotoUploading = false;
					});
			});
		}
	}

	onCountySelected(county: MatSelectChange): void {
		this.businessInformationForm.get('countyAreaId').enable();
		this.myProfileFacade.loadCountyAreaList(county?.value);

		this.searchCounty.patchValue('');
		this.searchCountyArea.patchValue('');
	}

	onCountyAreaSelected(): void {
		this.searchCountyArea.patchValue('');
	}

	patchBusinessProfileData(business: BusinessProfile) {
		if (business?.county?.id) {
			this.myProfileFacade.loadCountyAreaList(business?.county?.id);
		}

		if (!this.isAgentUser) {
			this.businessInformationForm.get('countyAreaId').enable();
		}

		const USER_DETAILS = {
			name: business?.name,
			countyId: business?.county?.id,
			countyAreaId: business?.countyArea?.id,
			address: business?.address,
			phone: business?.phone,
		};

		this.businessInformationForm.patchValue(USER_DETAILS);
		this.businessInformationForm.markAsPristine();
	}

	getUserDetails(): void {
		this.business$.pipe(takeUntil(this.destroy)).subscribe((business: BusinessProfile) => {
			if (business) {
				this.businessProfile = business;
				this.hasBusinessAccount = business?.name ? true : false;
				this.isAgentUser = !!business?.roles.find((role) => role === UserRoles.IS_AGENT_VERIFIED) && !!business?.name;

				if (this.isAgentUser) {
					this.businessInformationForm.disable();
				}

				if (business?.name) {
					this.patchBusinessProfileData(business);
				}
			} else {
				this.myProfileFacade.loadBusinessProfile();
			}
		});
	}

	updateEmail(): void {
		this.dialog.open(AuthDialogV2Component, {
			data: { authType: AuthType.CHANGE_EMAIL, email: this.businessProfile.email },
			panelClass: 'auth-dialog',
			disableClose: true,
		});
	}

	redirectToPlaceAd(): void {
		const FORM_DATA = this.businessInformationForm.value;
		this.myProfileFacade.updateUserBusinsessProfile(FORM_DATA).then(() => {
			this.placeAdService.addIndividualControlData(PlaceAdType?.SELLER_TYPE, SellerType.BUSINESS);

			if (!this.hasBusinessAccount) {
				this.myProfileFacade.setShowBusinessAcc(true);

				this.dialog
					.open(SubscriptionOfferDialogComponent, {
						disableClose: true,
						width: '500px',
						height: '600px',
					})
					.afterClosed();
			}
			this.router.navigate([APP_ROUTE.placeAdCategory], { queryParams: { sellerTypeId: SellerType.BUSINESS } });
		});

		this.businessInformationForm.markAsPristine();
	}

	mouseClick(): void {
		setTimeout(() => {
			this.phoneNumber?.focus();
		}, 200);
	}

	ngOnDestroy(): void {
		this.myProfileFacade.setProfileTabIndex(0);
	}

	ngOnInit(): void {
		this.myProfileFacade.loadCountyList();
		this.getUserDetails();
	}
}
